.navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    margin-top: 3rem;
  }
  
  .fa-firstdraft {
    margin-left: 0.5rem;
    font-size: 1.6rem;
  }
  
  .nav-item {
    display: flex;
    align-items: center;
    height: 80px;
  }
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(5, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    margin-right: 1rem;
  }
  .nav-links {
    color: rgb(97, 94, 94);
    text-decoration: none;
    padding: 0.5rem 1.5rem;
  }
  
  .nav-links:after {
    content: "";
    display: block;
    width: 0;
    transition: width 0.7s ease, background-color 0.5s ease;
    justify-self: center;
  }
  .nav-links:hover:after {
    width: 100%;
    background: #71bbe3;
    border-radius: 3px;
    padding: 2px;
  }
  .fa-bars {
    color: rgb(12, 12, 12);
  }
  
  .menu-icon {
    display: none;
  }
  
 
  .appheader p {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 4vmin);
    color: rgb(12, 12, 12);
    margin-top: 2rem;
    font-family: "Nobel Book", "san-serif";
  }
  /* media screen */
  @media screen and (max-width: 768px) {
    .dropdown-link {
      top: 2;
      left: 6;
    }
    .appheader p {
      font-size: calc(10px + 3vmin);
      margin-top: 5rem;
    }
    
    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        height: 100vh;
        position: absolute;
        top: 12%;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
        z-index:1;
      }
    
      
      .nav-item {
        position: relative;
        transition: all 0.5s ease;
      }
     
      .nav-menu.active {
        width: 100%;
        background: #242222;
        left: 0;
        opacity: 0.9;
        transition: all 0.5s ease;
        z-index: 1;
      }
      .nav-links {
        text-align: center;
        padding: 0.9rem;
        margin-top: 2rem;
        font-size: 0.89rem;
        display: table;
        width: 90%;
        transition: all 0.5s ease;
    
        color: white;
      }
      .nav-links:hover:after {
        width: 20%;
        text-align: center;
        margin-right:auto;
        margin-left:auto;
        margin-top:0;
      }
      .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: calc(10px + 3vmin);
        cursor: pointer;
        top: 7%;
      }
      .fa-times {
        color: rgb(12, 10, 10);
        transition: all 0.5s ease;
      }
      .fa-times {
        color: rgb(8, 8, 8);
      }
    
  }